.hidden {
  visibility: hidden;
}

.hcentered {
  text-align: center;
  margin: auto;
}

.hleft {
  text-align: left;
  margin: auto;
}

.vcentered {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

* {
  color: #ddd;
  font-family: monospace;
  font-weight: 700;
}

html, body {
  background-color: #111114;
  overflow: hidden;
}

button {
  border: 0;
  border-radius: .35rem;
  flex-grow: 1;
  margin: .5rem;
  padding: 1rem;
}

button.nav {
  background-color: #25252e;
  flex-grow: 0;
  width: 4rem;
  height: 4rem;
}

button.nav:hover {
  background-color: #1f1f27;
}

button.nav:active {
  background-color: #1a1a20;
}

button.nav:disabled {
  background-color: #4a4c52;
}

button.success {
  background-color: #00bd3f;
}

button.success:hover {
  background-color: #00a136;
}

button.success:active {
  background-color: #00842c;
}

button.success:disabled {
  background-color: #4a4c52;
}

button.danger {
  background-color: #bd1c00;
}

button.danger:hover {
  background-color: #a11800;
}

button.danger:active {
  background-color: #841400;
}

button.danger:disabled {
  background-color: #4a4c52;
}

h1.nav {
  flex-grow: 1;
}

.container {
  min-width: 100px;
  max-width: 900px;
  padding: 5px;
  display: flex;
}

.deck {
  border-radius: .35rem;
  width: 100%;
  min-width: 250px;
  padding-bottom: 75%;
  position: relative;
}

.card {
  perspective: 1000px;
  border-radius: .35rem;
  width: 100%;
  padding-bottom: 75%;
  position: absolute;
}

.card.slide-left {
  transition: all .5s;
  transform: translate(-150%, -100%)rotate(-90deg);
}

.card.slide-right {
  transition: all .5s;
  transform: translate(150%, -100%)rotate(90deg);
}

.cardFront, .cardBack {
  box-sizing: border-box;
  backface-visibility: hidden;
  background-color: #25252e;
  border-radius: .35rem;
  width: 100%;
  height: 100%;
  transition: transform .5s;
  position: absolute;
  box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
}

.cardFront {
  transform: rotateY(0);
}

.cardFront.flipped {
  transform: rotateY(-180deg);
}

.cardBack {
  background-color: #25252e;
  transform: rotateY(180deg);
}

.cardBack.flipped {
  transform: rotateY(0);
}

.score {
  width: 100%;
  margin: auto;
}

.score.success {
  color: #00bd3f;
}

.score.danger {
  color: #bd1c00;
}

.modal {
  z-index: 1000;
  background-color: #00000080;
  width: 100%;
  height: 100%;
  padding-top: 20%;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  z-index: 1001;
  background-color: #25252e;
  border-radius: .35rem;
  width: 50%;
  padding: 1rem;
}
